.Event-Monitor-main-content {
  display: flex;
  flex-direction: column;
}
.DatePickerFlex {
  flex-direction: column;
  display: flex;
}
.date-picker-area-From{
  padding: 5px;
    font-weight: 500;
    font-family: Roboto;
    font-size: 14px;
    line-height: 2.5;
    text-align: center;
    color: #000000;
}
.date-picker-area-To{
  padding: 5px;
    padding-bottom: 37px;
    font-weight: 500;
    font-family: Roboto;
    font-size: 14px;
    line-height: 2.5;
    text-align: center;
    color: #000000;
}
.date-picker-area {
  padding-bottom: 10px;
  padding-right: 5px;
  padding-left: 5px;
  justify-content: left;
  font-size: 12px;
  flex-direction: row;
}
.Event-monitor-partone {
  width: 100%;
    height: 40.5px;
    background-color: #f7f7f7;
    position: relative;
    display: flex;
    cursor: pointer;
}
.EventFlex {
  display: flex;
}
.Event-monitor-search-header {
  position: relative;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
  flex: 2 1;
  .Event-monitor-search-icon {
    position: absolute;
    width: 16px;
    height: 26px;
    line-height: 26px;
    color: #999999;
    left: 7px;
  }

  .EventMonitorSearchBar {
    height: 22px;
    border-radius: 30px;
    border: solid 0.5px #f0f0f0;
    background-color: #ffffff;
    font-size: 12px;
    color: #999999;
    padding-left: 31px;
    padding-top: 6px;
    padding-bottom: 6px;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: left;
    box-sizing: border-box;
    width: 100%;
  }
}
.Event-monitor-filter{
  margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
}
.middle-space {
  width: 100%;
  height: 0;
  border: solid 0.5px hsl(0, 0%, 91%);
}

.Event-monitor-parttwo {
  // display: flex;
  // flex-direction: column;
  width: 100%;
  background-color: hsl(0, 0%, 97%);
  padding-top: 20px;
}
.Event-monitor-bar-container {
  display: flex;
  flex-wrap: nowrap;
  height: 100px;
  overflow-y: scroll;

  .chart-data {
    display: flex;
    position: relative;
  }
  .chart-date {
    position: absolute;
    left: 20px;
    font-size: 13px;
    white-space: nowrap;
    color: #999999;
  }
  .chart-bar-wrapper {
    width: 40px;
    height: 65px;

    cursor: pointer;
    position: relative;
    margin: 20px;
    overflow: hidden;
    border-bottom: 2px solid transparent;
    .Event-monitor-bar {
      position: absolute;
      width: 10px;
      height: 100%;
      left: 0;
      right: 0;
      bottom: 15px;
      margin: auto;
      background-color: #4a51c3;
      transition: all 0.5s;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }
    .Event-monitor-time {
      position: absolute;
      bottom: 0px;
      left: 0;
      right: 0;
      margin: auto;
      text-align: center;
      overflow: hidden;
      font-weight: 500;
      font-size: 12px;
    }

    &.activeExceptionChart {
      border-bottom: 2px solid #4a51c3;
      background-color: #ebedfd;
    }
  }
}
.Event-monitor-partthree {
  // height: auto;
  // overflow-x: scroll;
  // width: calc(367vh - 114px);
  overflow: scroll; 
  height: calc(75vh - 114px);
}

.Event-monitor-label {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  width: 99%;
  font-weight: 500;
  align-items: center;
  padding: 0px 18px;
  /* border: solid 1px #d9d9d9; */
  cursor: pointer;
  /* border-radius: 10px; */
  padding: 12px 25.5px 12px 18px;
}

.Event-monitor-slider {
  width: 100%;
  display: flex;
  flex-direction: row;
  font-size: 14px;
  cursor: pointer;
  font-family: Roboto;
  /* text-align: left; */
  color: var(--black);

  cursor: pointer;
  font-family: Roboto;

  text-align: left;
  color: var(--black);
  .Event-Monitor-Details {
    display: flex;
    flex: 1;
  }
  .Event-monitor-errorLayer {
    display: flex;
  }
  .Event-monitor-name {
    font-family: Roboto;
    font-size: 12px;
    width: 788px;
    /* flex: 2; */
    letter-spacing: normal;
    text-align: left;
    color: var(--black);
  }
  .Event-monitor-error {
    font-family: Roboto;
    font-size: 13px;

    height: 7px;

    text-align: left;
    color: var(--black);
  }
  .Event-monitor-desc {
    font-family: Roboto;
    font-size: 12px;
    padding-top: 8px;
    text-align: left;
    color: #666666;
  }
  .Event-monitor-outlayer {
    border-radius: 15px;
    border: solid 0.5px #e3e3e3;
    background-color: #f7f7f7;
    /* width: 41%; */
    height: 21px;
    /* flex: 2; */
    /* margin-right: 95px; */
    margin-top: 5px;
    /* margin-left: 666px; */
    .Event-monitor-description {
      font-family: Roboto;
      font-size: 9px;
      width: 87px;
      margin-top: 2px;
      text-align: center;
      text-transform: uppercase;
      /* font-weight: normal; */
      /* font-stretch: normal; */
      /* font-style: normal; */
      color: #666666;
      padding: 5px;
      color: #666666;
      margin-top: 0px;
    }
  }

  .Event-monitor-datetime {
    font-family: Roboto;
    font-size: 12px;
    text-align: left;
    width: 170px;
    padding-left: 26px;
    margin-top: 5px;
    /* margin-right: 30px; */
    justify-content: flex-end;
    color: #666666;
    /* flex: 2; */
  }
}

