.main-error-tab {
  
  border: solid 1px #e7e8f7;
  margin: 72px 10px 10px 10px;
  

  .tab-error-main-div {
    display: flex;
    width: 100%;
  }

  .tab-list {
    
    height: 40px;
    font-family: Roboto;
    display: flex;
    padding-left: 5px;
    flex-direction: row;
    padding-left: 18.6%;
    align-items: center;
    border-bottom: solid 0.5px #ededed;
    cursor: pointer;
    color: #4a51c3;
    background-color: #ffffff;
    flex: 1 1;
  }

  .tablist:last-child {
    border-bottom: 1px solid #e7e8f7;
  }

  .tab-panel {
    // margin-left: 27.5%;
    background-color: #fff;
  }
  .slider:hover {
    background-color: #ebedfd;
    border-bottom: solid 0.5px #ededed;
  }
  .react-tabs__tab--selected {
    background-color: #4a51c3;
    color: white;
    border-radius: 0px;
  }
  .tab-head-div {
    color: #666666;
    padding-left: 10%;
    padding-bottom: 6%;
    padding-top: 1%;
  }
}
